<template>
    <div class="container" ref="container">
        <div class="ui">aa</div>
        <Scene :position="position"></Scene>
        <!--<header>PGS Sun Position</header>
        <div class="Body">
            <main class="Content">
                <Scene :position="position"></Scene>
            </main>
            <nav class="Nav">
                <LeafletMap @updatePosition="updatePosition"></LeafletMap>
                <SunChart></SunChart>
            </nav>
        </div>
        <!--<footer>some footer here</footer>-->
    </div>
</template>

<script>
    /* eslint-disable */
    import LeafletMap from './LeafletMap';
    import Scene from './Scene';
    import SunChart from './SunChart';

    const defaultPosition = {
        lat: 51.096474713,
        lng: 17.034752369,
    };

    export default {
        name: 'Main',
        components: {
            LeafletMap,
            Scene,
            SunChart,
        },
        data() {
            return {
                position: defaultPosition,
            };
        },
        methods: {
            updatePosition(newPosition) {
                this.position = newPosition;
            },
        },
    };
</script>

<style scoped>
    .container {
        flex-grow: 1;
        display: flex;
        min-height: 100vh;
        flex-direction: row;
    }

    header {
        height: 80px;
        color: #ecf0f1;
        font-family: 'Merriweather', serif;
        font-weight: 700;
        line-height: 80px;
        font-size: 30px;

        text-shadow: 2px 2px 0px rgba(0,0,0,0.2);
    }

    footer {
        height: 55px;
    }


    .ui {
        flex: 1;
    }

    .Body {
        display: flex;
        flex: 1;
        /*background-image: linear-gradient(0 deg, #ecf0f1 0 %, #d9dfe3 100 %);*/
    }

    .Content {
        flex: 1;
        align-items: stretch;
        display: flex;
        padding: 10px 10px 10px 0;
    }

    .Nav {
        flex: 0 0 300px;
        order: -1;
        display: flex;
        flex-direction: column;
        padding: 10px;
    }
</style>



