<template>
    <div id="app">
        <router-view></router-view>
    </div>
</template>

<script>
    export default {
        name: 'app',
    };
</script>

<style lang="scss">
    @import url('https://fonts.googleapis.com/css?family=Merriweather|Roboto:400,700');
    @import url('https://fonts.googleapis.com/css?family=Lato:100,400');

    html, body {
        height: 100vh;
        margin: 0;
        overflow: hidden;
        display: flex;
        flex-direction: column;
        align-items: stretch;
        /*background-image: radial-gradient(ellipse closest-corner at center, #4C3D67 0%, #1A1524 100%);*/

        /*background: linear-gradient(to right, #4e4376, #2b5876); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

        background: radial-gradient(circle, #2b5876, #4e4376);
        background: radial-gradient(circle farthest-side at 75% 50%, #7D4E74 30%, #3A1C71 100%);

    }

    #app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
        display: flex;
        flex-grow: 1;
    }
</style>



